
.header{
  grid-template-columns: header;
  
}


.menu {
  height: 66px;
  margin: 20px auto;
  position: relative;
  margin-bottom: 100px;
}
.menu li:nth-child(4) {
  margin-right: 150px;
}
.menu li:nth-child(6) a {
  margin-right: 0;
}
.menu li:nth-child(1n + 4) a {
  text-align: right;
}
.list {
  margin: 20px;
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  list-style: none;
}
.item {
  margin-left: 50px;
  display: inline;
  float: left;
}
.item a {
  display: block;
  padding: 10px 0;
  text-decoration: none;
  margin: 10px 10px 0 0;
  width: 100px;
  color: rgb(100, 71, 71);
}
.logo {
  position: relative;
  top: -30px;
  left: 700px;
  width: 250px;
  height: 200px;
  float:left;
  bottom:80px;
}

.nav_icons{
  cursor:pointer;
  list-style: none;
  display: none;
}
@media screen  and (max-width:768px){
  .nav_icons{
    display:block;
    cursor:pointer;
  }
  .list{
    position:fixed;
    top:7vh;
    right: 0;
    width: 50%;
    height: 93vh;
    background-color: antiquewhite;
    flex-direction: column;
    transform: translateX(100%);
  }
  

}
.nav__active{
  transform: translateX(0);
}
