.mission {
  background-color: antiquewhite;
  width: 1500px;
  margin:0 auto;

  &__text {
    text-align: center;
    margin: 100px;
    justify-content: center;
    font-size: 20px;
  }
  &__title {
    text-align: center;
    font-size: 30px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
  }
}
.container {
  display: flex;
  width: 100%;
  height: 100vh;
  
 perspective: 1000px;
 
}

.card {
  position: relative;
  width: 9rem;
  height: 55rem;
  margin-left: 150px;
  text-align: center;
  line-height: 8rem;
  color: #ecf0f1;
  border-radius: 0.8rem;
 transform-style: preserve-3d;
 
}

.face {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 0.8rem;
  background-size: cover;
  background-position: center center;
  backface-visibility: hidden;
  transition: transform 0.5s ease-in-out;
 
}

.face--front img {
  width: 250px;
  border-radius: 0.8rem;

}

.face--back {
  background: #91846b;
  font-size: 1.5rem;
  transform: rotateY(180deg);
  width: 250px;
  height: 120px;
}

.card:hover .face--front {
  transform: rotateY(-180deg);
}

.card:hover .face--back {
  transform: rotateY(0deg);
}

