.services{
    background-color: antiquewhite;
    width: 1500px;
    margin:0 auto;
    flex-direction: row ;
    transition: transform .2s; /* Animation */
    padding-top: 150px;
&__title{
    text-align: center;
    font-size: 30px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
&__image{
    width: 250px;
    border-radius: 10%;
}

&__text{
   
}
&__column{
 display:flex;
 flex-wrap: wrap;
 text-decoration: none;
 width: 650px;
 margin:0 auto; 
 align-items: center; 
 padding: 20px;
 list-style: none;
 
}
&__column li{
margin-left: 50px;
-webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}
&__column li :hover{
    transform: scale(1.5);
}
}