.menuburger{
    width: 100vw;
    height: 100vh;
    background-color: antiquewhite;
    z-index: 100;
}
.nav_icons{
    cursor:pointer;
    list-style: none;
    display: none;
  }
  @media screen  and (max-width:768px){
    .nav_icons{
      display:block;
      cursor:pointer;
    }
    .list{
      position:fixed;
      top:7vh;
      right: 0;
      width: 50%;
      height: 93vh;
      background-color: antiquewhite;
      flex-direction: column;
      transform: translateX(100%);
    }
    
  
  }
  .nav__active{
    transform: translateX(0);
  }